import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { first } from 'rxjs/operators';

import { UserService } from '../_services';
import { environment } from '../../environments/environment';

declare var $: any;
@Component({
  selector: 'app-add-kitchen',
  templateUrl: './add_kitchen.component.html',
  styleUrls: [ './add_kitchen.component.css' ]
})
export class AddKitchenComponent implements OnInit {
  base_url:string = environment.base_url;
  kitchenId:any;
  response:any;
  model:any = {};
  isLoader = true;
  constructor(
	  private router: Router,
	  private activatedRouter: ActivatedRoute,
	  private userService: UserService
  ) {
	  //s
  }

  ngOnInit() {
	  this.activatedRouter.params.subscribe(params => {
	       this.kitchenId = +params['id'];
		   console.log(this.kitchenId);
		   this.openLoginModel(this.kitchenId);
	    });
  }

  pressBackButton(){
	  this.router.navigate(['/kitchen']);
  }

  openLoginModel(kitchenId){
	  if(kitchenId){
		  this.userService.getKitchenDetails(kitchenId)
			  .pipe(first())
			  .subscribe(
				  data => {
					  this.response = data;
					  console.log(this.response);
					  if(this.response.error_code == 0){
						  this.model = this.response.data;
					  }else{
						  alert(this.response.message);
						  this.router.navigate(['/kitchen']);
					  }
					  this.isLoader = false;
				  },
				  error => {
					  console.log(error);
					  alert("Problem in api");
					  this.isLoader = false;
				  });
	  }else{
		  this.model = {};
		  this.isLoader = false;
	  }
  }

  onSubmit() {
	  console.log(this.model);
	  this.isLoader = true;
	  if(!this.model.id){
		  this.model.id = 0;
	  }
	  this.userService.addEditkitchen(this.model)
		  .pipe(first())
		  .subscribe(
			  data => {
				  this.response = data;
				  if(this.response.error_code == 0){
					  this.router.navigate(['/kitchen']);
				  }else{
					  this.router.navigate(['/login']);
				  }
				  console.log(data);
				  this.isLoader = false;
			  },
			  error => {
				  console.log(error);
				  alert("Problem in api");
				  this.isLoader = false;
			  });
  }
}
