import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { first } from 'rxjs/operators';

import { KitchenService } from '../_services';
import { environment } from '../../environments/environment';

declare var $: any;
@Component({
  selector: 'app-kitchen-login',
  templateUrl: './kitchen_login.component.html',
  styleUrls: [ './kitchen_login.component.css' ]
})
export class KitchenLoginComponent implements OnInit {
  model:any = {};
  base_url:string = environment.base_url;
  isLoader = false;
  response:any;

  constructor(private router: Router,private kitchenService: KitchenService) { }

  ngOnInit() {
	  if(localStorage.getItem('oml_kitchen_user_details')){
		  this.router.navigate(['/kitchen_dashboard']);
	  }
  }

  openLoginModel(){

  }

  onSubmit() {
	  console.log(this.model);
	  this.isLoader = true;
	  this.kitchenService.login(this.model)
        .pipe(first())
        .subscribe(
            data => {
				this.response = data;
				if(this.response.error_code == 0){
					localStorage.setItem('oml_kitchen_user_details', JSON.stringify(this.response.data));
					this.router.navigate(['/kitchen_dashboard']);
				}else{
					alert(this.response.message);
				}
              console.log(this.response);
			  this.isLoader = false;
            },
            error => {
                console.log(error);
				alert("Problem in api");
				this.isLoader = false;
            });
  }
}
