﻿import { NgModule }      from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule  }    from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';

// used to create fake backend
import { fakeBackendProvider } from './_helpers';

import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';

import { AppComponent }  from './app.component';
import { routing }        from './app.routing';

import { AlertComponent } from './_directives';
import { AuthGuard,AuthKitchenGuard } from './_guards';
import { JwtInterceptor, ErrorInterceptor } from './_helpers';
import { AlertService, AuthenticationService, UserService,KitchenService } from './_services';

import { RegistrationComponent }   from './registration';
import { HomeComponent }   from './home';
import { HeaderComponent }   from './header';
import { FooterComponent }   from './footer';
import { SideMenuComponent }   from './side_menu';
import { LoginComponent }   from './login';
import { BrowseKitchenComponent }   from './browse_kitchen';
import { AddProductComponent }   from './add_product';
import { AddKitchenComponent }   from './add_kitchen';
import { UserComponent }   from './user';
import { AddUserComponent }   from './add_user';

import { KitchenComponent }   from './kitchen';

import { ProductComponent }   from './product';
import { KitchenHeaderComponent }   from './kitchen_header';
import { KitchenSideMenuComponent }   from './kitchen_side_menu';
import { KitchenLoginComponent }   from './kitchen_login';
import { KitchenDashboardComponent }   from './kitchen_dashboard';

@NgModule({
    imports: [
        BrowserModule,
        FormsModule,
        HttpClientModule,
        routing,
		NgMultiSelectDropDownModule.forRoot()
    ],
    declarations: [
        AppComponent,
        AlertComponent,
        RegistrationComponent,
        HomeComponent,
		HeaderComponent,
		FooterComponent,
		BrowseKitchenComponent,
		KitchenComponent,
		LoginComponent,
		AddProductComponent,
		AddKitchenComponent,
		AddUserComponent,
		UserComponent,

		ProductComponent,
		KitchenHeaderComponent,
		KitchenSideMenuComponent,
		KitchenLoginComponent,
		KitchenDashboardComponent
    ],
    providers: [
        AuthGuard,
		AuthKitchenGuard,
		KitchenService,
        AlertService,
        AuthenticationService,
        UserService,
        { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
        { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },

        // provider used to create fake backend
        fakeBackendProvider
    ],
    bootstrap: [AppComponent]
})

export class AppModule { }
