import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { first } from 'rxjs/operators';

import { KitchenService } from '../_services';
import { environment } from '../../environments/environment';

declare var $: any;
@Component({
  selector: 'app-kitchen-dashboard',
  templateUrl: './kitchen_dashboard.component.html',
  styleUrls: [ './kitchen_dashboard.component.css' ]
})
export class KitchenDashboardComponent implements OnInit {
  model:any = {};
  base_url:string = environment.base_url;
  isLoader = false;
  response:any;
  kitchenUserList:any = [];
  currentUser:any;

  constructor(private router: Router,private kitchenService: KitchenService) {
	  this.currentUser = JSON.parse(localStorage.getItem('oml_kitchen_user_details'));
	  if(this.currentUser.kitchen_id){
		  //this.router.navigate(['/product']);
	  }
  }

  ngOnInit() {
	  this.kitchenService.getKitchenList()
	  .pipe(first())
	  .subscribe(
		  data => {
			  this.response = data;
			  if(this.response.error_code == 0){
				  this.kitchenUserList = this.response.data;
			  }else{
				  alert(this.response.message);
			  }
			console.log(this.response);
			this.isLoader = false;
		  },
		  error => {
			  console.log(error);
			  alert("Problem in api");
			  this.isLoader = false;
		  });
  }

  openLoginModel(){

  }

  onClickKitchen(kitchenId) {
	  this.currentUser.kitchen_id = kitchenId;
	  localStorage.setItem('oml_kitchen_user_details', JSON.stringify(this.currentUser));
	  this.router.navigate(['/product']);
  }
}
