import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { first } from 'rxjs/operators';

import { UserService } from '../_services';
import { environment } from '../../environments/environment';

declare var $: any;
@Component({
  selector: 'app-add-user',
  templateUrl: './add_user.component.html',
  styleUrls: [ './add_user.component.css' ]
})
export class AddUserComponent implements OnInit {
  base_url:string = environment.base_url;
  kitchenId:any;
  response:any;
  model:any = {};
  isLoader = true;
  kitchenList:any;
  dropdownSettings:any = {
        singleSelection: false,
        idField: 'id',
        textField: 'name',
        selectAllText: 'Select All',
        unSelectAllText: 'UnSelect All',
        itemsShowLimit: 100,
        allowSearchFilter: false
      };
  constructor(
	  private router: Router,
	  private activatedRouter: ActivatedRoute,
	  private userService: UserService
  ) {
	  //s
  }

  ngOnInit() {
	  this.activatedRouter.params.subscribe(params => {
	       this.kitchenId = +params['id'];
		   console.log(this.kitchenId);
		   this.openLoginModel(this.kitchenId);
	    });
  }

  pressBackButton(){
	  this.router.navigate(['/kitchen']);
  }

  openLoginModel(kitchenId){
	  if(kitchenId){
		  this.userService.getUserDetails(kitchenId)
			  .pipe(first())
			  .subscribe(
				  data => {
					  this.response = data;
					  console.log(this.response);
					  if(this.response.error_code == 0){
						  this.model = this.response.data;
						   var tempData = [];
						  this.response.data.kitchen.forEach(function(val){
							 console.log(val);
							 tempData.push(val.kitchen);
						  });
						  this.model.kitchen_ids = tempData;
					  }else{
						  alert(this.response.message);
						  this.router.navigate(['/kitchen']);
					  }
					  this.isLoader = false;
				  },
				  error => {
					  console.log(error);
					  alert("Problem in api");
					  this.isLoader = false;
				  });
	  }else{
		  this.model = {};
		  this.model.kitchen_ids = [];
		  this.isLoader = false;
	  }

	  this.userService.getKitchenList()
		.pipe(first())
		.subscribe(
			data => {
			  this.response = data;
			  if(this.response.error_code == 0){
				  console.log(this.response);
				  this.kitchenList = this.response.data;
			  }
			  this.isLoader = false;
			},
			error => {
				console.log(error);
			  	alert("Problem in api");
			  	this.isLoader = false;
			});
  }

  onSubmit() {
	  console.log(this.model);
	  this.isLoader = true;
	  if(!this.model.id){
		  this.model.id = 0;
	  }
	  delete this.model.kitchen;
	  this.userService.addEditUser(this.model)
		  .pipe(first())
		  .subscribe(
			  data => {
				  this.response = data;
				  if(this.response.error_code == 0){
					  this.router.navigate(['/user']);
				  }else{
					  alert(this.response.message);
					  //this.router.navigate(['/login']);
				  }
				  console.log(data);
				  this.isLoader = false;
			  },
			  error => {
				  console.log(error);
				  alert("Problem in api");
				  this.isLoader = false;
			  });
  }
}
