﻿import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { environment } from '../../environments/environment';
import { User } from '../_models';

@Injectable()
export class UserService {
	currentUser:any;
    constructor(private http: HttpClient) {
		this.currentUser = JSON.parse(localStorage.getItem('oml_user_details'));
	}

    getKitchenList() {
		var data = {
			"access_token":this.currentUser.access_token
		}
        return this.http.post(`${environment.apiUrl}/vender/kitchen/list`, data);
    }

    getKitchenDetails(id) {
		var data = {
			"access_token":this.currentUser.access_token,
			"id":id
		}
        return this.http.post(`${environment.apiUrl}/vender/kitchen/detail`, data);
    }

    addEditkitchen(data: any) {
		data.access_token = this.currentUser.access_token;
        return this.http.post(`${environment.apiUrl}/vender/kitchen/add_edit`, data);
    }

    delete(id: number) {
        return this.http.delete(`${environment.apiUrl}/users/` + id);
    }

	getUserList() {
		var data = {
			"access_token":this.currentUser.access_token
		}
        return this.http.post(`${environment.apiUrl}/vender/user/list`, data);
    }

    getUserDetails(id) {
		var data = {
			"access_token":this.currentUser.access_token,
			"id":id
		}
        return this.http.post(`${environment.apiUrl}/vender/user/detail`, data);
    }

    addEditUser(data: any) {
		data.access_token = this.currentUser.access_token;
        return this.http.post(`${environment.apiUrl}/vender/user/add_edit`, data);
    }

	deleteUser(id:any){
		
	}
}
