import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { first } from 'rxjs/operators';

import { KitchenService } from '../_services';
import { environment } from '../../environments/environment';

declare var $: any;
@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: [ './product.component.css' ]
})
export class ProductComponent implements OnInit {
  base_url:string = environment.base_url;
  productList:any;
  response:any;
  isLoader = false;

  constructor(private router: Router,private kitchenService: KitchenService) { }

  ngOnInit() {
	  this.kitchenService.getProductList()
	  .pipe(first())
	  .subscribe(
		  data => {
			  this.response = data;
			  if(this.response.error_code == 0){
				  this.productList = this.response.data;
			  }else{
				  alert(this.response.message);
			  }
			console.log(this.response);
			this.isLoader = false;
		  },
		  error => {
			  console.log(error);
			  alert("Problem in api");
			  this.isLoader = false;
		  });
  }

  openLoginModel(){
    $('#login').modal('show');
  }

  onSubmit() {

  }
}
