import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { first } from 'rxjs/operators';

import { KitchenService } from '../_services';
import { environment } from '../../environments/environment';

declare var $: any;
@Component({
  selector: 'app-add-product',
  templateUrl: './add_product.component.html',
  styleUrls: [ './add_product.component.css' ]
})
export class AddProductComponent implements OnInit {
  base_url:string = environment.base_url;
  productList:any;
  response:any;
  productTypeList:any = [];
  productLoactionList:any = [];
  productTimesList:any = [];
  isLoader = false;
  model:any = {};

  constructor(private router: Router,private kitchenService: KitchenService) { }

  ngOnInit() {
	  this.kitchenService.getAddEditData()
	  	.pipe(first())
		.subscribe(
			data => {
				this.response = data;
				if(this.response.error_code == 0){
					this.productTypeList = this.response.data.product_types;
					this.productLoactionList = this.response.data.product_locations;
					this.productTimesList = this.response.data.product_times;
				}else{
					alert(this.response.message);
				}
			  console.log(this.response);
			  this.isLoader = false;
			},
			error => {
				console.log(error);
				alert("Problem in api");
				this.isLoader = false;
			});
  }

  pressBackButton(){
	  this.router.navigate(['/product']);
  }

  openLoginModel(){

  }

  onSubmit() {

  }
}
