﻿import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { environment } from '../../environments/environment';

@Injectable()
export class AuthenticationService {
    constructor(private http: HttpClient) { }

    login(data: any) {
        let headers = new HttpHeaders({
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Access-Control-Allow-Origin':'*'
        });
        let options = { headers: headers };
        return this.http.post(`${environment.apiUrl}/vender/login`, data);

    }

    registration(data: any) {
        return this.http.post(`${environment.apiUrl}/Registration`, data );

    }

    checkPoint() {
        return this.http.get(`${environment.apiUrl}/CheckPoint`);
    }

    professionalQualification() {
        return this.http.get(`${environment.apiUrl}/ProfessionalQualification`);
    }

    specificTraining() {
        return this.http.get(`${environment.apiUrl}/SpecificTraining `);
    }

    hhs() {
        return this.http.get(`${environment.apiUrl}/HHS`);
    }

    positionTitle() {
        return this.http.get(`${environment.apiUrl}/PositionTitle`);
    }

    stream() {
        return this.http.get(`${environment.apiUrl}/Stream`);
    }

    paypointGroup() {
        return this.http.get(`${environment.apiUrl}/PaypointGroup`);
    }

    availability() {
        return this.http.get(`${environment.apiUrl}/Availability`);
    }

    leadTime() {
        return this.http.get(`${environment.apiUrl}/LeadTime`);
    }

    logout() {
        // remove user from local storage to log user out
        localStorage.removeItem('currentUser');
    }
}
