﻿import { Routes, RouterModule } from '@angular/router';

import { AuthGuard,AuthKitchenGuard } from './_guards';

import { RegistrationComponent }   from './registration';
import { HomeComponent }   from './home';
import { HeaderComponent }   from './header';
import { BrowseKitchenComponent }   from './browse_kitchen';
import { LoginComponent }   from './login';
import { KitchenComponent }   from './kitchen';
import { ProductComponent }   from './product';
import { AddProductComponent }   from './add_product';
import { AddKitchenComponent }   from './add_kitchen';
import { UserComponent }   from './user';
import { AddUserComponent }   from './add_user';


import { KitchenLoginComponent }   from './kitchen_login';
import { KitchenDashboardComponent }   from './kitchen_dashboard';

const appRoutes: Routes = [
  { path: 'registration', component: RegistrationComponent},
  { path: 'browse_kitchen', component: BrowseKitchenComponent, canActivate: [AuthGuard] },
  { path: 'kitchen', component: KitchenComponent, canActivate: [AuthGuard] },
  { path: 'user', component: UserComponent, canActivate: [AuthGuard] },
  { path: 'product', component: ProductComponent, canActivate: [AuthKitchenGuard] },
  { path: 'login', component: LoginComponent},
  { path: 'kitchen_login', component: KitchenLoginComponent},
  { path: 'kitchen_dashboard', component: KitchenDashboardComponent, canActivate: [AuthKitchenGuard]},
  { path: 'add_user/:id', component: AddUserComponent, canActivate: [AuthGuard] },
  { path: 'add_product/:id', component: AddProductComponent, canActivate: [AuthKitchenGuard] },
  { path: 'add_kitchen/:id', component: AddKitchenComponent, canActivate: [AuthGuard] },
  { path: '', component: HomeComponent, canActivate: [AuthGuard] }
];

export const routing = RouterModule.forRoot(appRoutes);
