﻿import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';

import { environment } from '../../environments/environment';
import { User } from '../_models';

@Injectable()
export class KitchenService {
	currentUser:any;
    constructor(private http: HttpClient) {
		this.currentUser = JSON.parse(localStorage.getItem('oml_kitchen_user_details'));
	}

	login(data: any) {
        let headers = new HttpHeaders({
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Access-Control-Allow-Origin':'*'
        });
        let options = { headers: headers };
		console.log(data);
        return this.http.post(`${environment.apiUrl}/vender/kitchen_login`, data);

    }

	getKitchenList() {
		var data = {
			"access_token":this.currentUser.access_token
		}
        return this.http.post(`${environment.apiUrl}/vender/kitchen_user`, data);
    }

    getProductList() {
		var data = {
			"access_token":this.currentUser.access_token,
			"kitchen_id":this.currentUser.kitchen_id
		}
        return this.http.post(`${environment.apiUrl}/vender/product/list`, data);
    }

    getAddEditData() {
		var data = {
			"access_token":this.currentUser.access_token,
			"kitchen_id":this.currentUser.kitchen_id
		}
        return this.http.post(`${environment.apiUrl}/vender/product/add_edit_data`, data);
    }

    addEditProduct(data: any) {
		data.access_token = this.currentUser.access_token;
        return this.http.post(`${environment.apiUrl}/vender/product/add_edit`, data);
    }

	deleteProduct(id:any){

	}
}
